import Vue, { AsyncComponent } from 'vue'
import Router, { RouteConfig, Route, NavigationGuard } from 'vue-router'

const Main: AsyncComponent = (): any => import('@/components/blog/Main.vue')
const First: AsyncComponent = (): any => import('@/components/blog/First.vue')
const Editor: AsyncComponent = (): any => import('@/components/blog/Editor.vue')
const Article: AsyncComponent = (): any => import('@/components/blog/Article.vue')

const ProjectBoard: AsyncComponent = (): any => import('@/components/cardboard/ProjectBoard.vue')
// const CardBoard: AsyncComponent = (): any => import('@/components/CardBoard.vue')

Vue.use(Router)

const routes: RouteConfig[] = [
  {
    path: '/',
    name: 'Main',
    component: Main,
    children: [
      {
        path: '/',
        name: 'First',
        component: First,

      },
    ]
  },

  // {
  //   path: '/editor',
  //   name: 'Editor',
  //   component: Editor
  // },
  // {
  //   path: '/taskboard',
  //   name: 'ProjectBoard',
  //   component: ProjectBoard
  // }
]

const router: Router = new Router({
  mode: 'history',
  base: '/',
  routes
})

export default router



