export module me {
    export class _Event {
        private map: any = {};
        constructor() {
            this.map = {};
        }
        public on(eventType: string, func: Function) {
            if (eventType == null || func == null) {
                console.log("eventType or func in Event.on canonnt be empty")
                return
            }
            if (this.map[eventType] == null) {
                let a: any = [];
                a.push(func);
                this.map[eventType] = a;
            }
            else {
                this.map[eventType].push(func);
            }
        }
        public cut(eventType: string, func: Function) {
            if (eventType == null || func == null) {
                console.log("eventType or func in Event.on canonnt be empty")
                return
            }
            let a = this.map[eventType];
            if (a != null) {
                for (const k in a) {
                    let v = a[k];
                    if (v == func) {
                        a[k] = null;
                    }
                }
            }
        }
        public emit(eventType: string, data?: any) {
            if (eventType != null) {
                let a = this.map[eventType];
                if (a != null) {
                    for (const k in a) {
                        a[k](data);
                    }
                }
            }
        }
    }
    export let Event = new me._Event();
}



