

























import Vue from "vue";
import Component from "vue-class-component";
import { Prop, Emit, Inject } from "vue-property-decorator";
import Myform from "./Myform.vue";
import { me } from "../../core/event";
@Component({
  components: { Myform }
})
export default class Alert extends Vue {
  obj: any = { isDisplay: false };
  mounted() {
    this.$nextTick(() => {
      me.Event.on("showalert", (event: any) => {
        console.log(event);
        this.showAlert(event);
      });
    });
  }
  checkEmpty = (): boolean => {
    for (const key in this.config.list) {
      let v = this.config.list[key];
      if (v.value === "") return true;
    }
    return false;
  };
  public showAlert(config: any) {
    this.config.title = config.title || this.config.title;
    this.config.info = config.info || this.config.info;
    this.config.okCall = config.okCall || this.config.okCall;
    this.config.ok = config.ok || this.config.ok;
    this.config.cannelCall = config.cannelCall || this.config.cannelCall;
    this.config.cannel = config.cannel || this.config.cannel;
    this.config.list = config.list || this.config.list;
    this.obj.isDisplay = true;
  }
  hide() {
    this.obj.isDisplay = false;
  }
  //   @Prop(Object)
  config: any = {
    title: "",
    info: "",
    okCall: () => {},
    ok: "确定",
    cannelCall: () => {},
    cannel: "取消",
    list: {}
  };
  ok = async () => {
    await this.config.okCall(this.config);
    this.obj.isDisplay = false;
  };
  cannel = async () => {
    await this.config.cannelCall(this.config);
    this.obj.isDisplay = false;
  };
}



